              description: '<p>Draws a point, a coordinate in space at the dimension of one pixel.\nThe first parameter is the horizontal value for the point, the second\nvalue is the vertical value for the point. The color of the point is\nchanged with the <a href="#/p5/stroke">stroke()</a> function. The size of the point\nis changed with the <a href="#/p5/strokeWeight">strokeWeight()</a> function.</p>\n',
