              file: "src/events/mouse.js",
              line: 1046,
              description: '<p>The function <a href="#/p5/exitPointerLock">exitPointerLock()</a>\nexits a previously triggered <a href="#/p5/requestPointerLock">pointer Lock</a>\nfor example to make ui elements usable etc',
              itemtype: "method",
              name: "exitPointerLock",
              example: ['\n<div class="notest">\n<code>\n//click the canvas to lock the pointer\n//click again to exit (otherwise escape)\nlet locked = false;\nfunction draw() {\n  background(237, 34, 93);\n}\nfunction mouseClicked() {\n  if (!locked) {\n    locked = true;\n    requestPointerLock();\n  } else {\n    exitPointerLock();\n    locked = false;\n  }\n}\n</code>\n</div>'],
