              description: '<p>Facilitates exponential expressions. The <a href="#/p5/pow">pow()</a> function is an efficient\nway of multiplying numbers by themselves (or their reciprocals) in large\nquantities. For example, pow(3, 5) is equivalent to the expression\n3 &times; 3 &times; 3 &times; 3 &times; 3 and pow(3, -5) is equivalent to 1 /\n3 &times; 3 &times; 3 &times; 3 &times; 3. Maps to\nMath.pow().</p>\n',
              itemtype: "method",
              name: "pow",
              params: [{
                name: "n",
                description: "<p>base of the exponential expression</p>\n",
                type: "Number"
              }, {
                name: "e",
                description: "<p>power by which to raise the base</p>\n",
                type: "Number"
              }],
              "return": {
                description: "n^e",
                type: "Number"
              },
              example: ["\n<div><code>\nfunction setup() {\n  //Exponentially increase the size of an ellipse.\n  let eSize = 3; // Original Size\n  let eLoc = 10; // Original Location\n\n  ellipse(eLoc, eLoc, eSize, eSize);\n\n  ellipse(eLoc * 2, eLoc * 2, pow(eSize, 2), pow(eSize, 2));\n\n  ellipse(eLoc * 4, eLoc * 4, pow(eSize, 3), pow(eSize, 3));\n\n  ellipse(eLoc * 8, eLoc * 8, pow(eSize, 4), pow(eSize, 4));\n}\n</code></div>"],
